
@import url(../node_modules/material-design-icons/iconfont/material-icons.css);
@import url(../node_modules/vuetify/dist/vuetify.min.css);
@import url(../node_modules/sanitize.css/sanitize.css);
#app{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#000000
}
header{font-family:'Roboto', sans-serif
}
main{font-family:'Open Sans', sans-serif
}
.content{overflow-y:scroll
}
.slide-enter-active{transition:all .3s ease
}
.slide-enter,.slide-leave-active{opacity:0
}
.slide-enter{transform:translateX(-3rem)
}
.slide-leave-active{transition:all .2s ease;transform:translateX(3rem)
}
.input-group label{color:black
}
.input-group:after{background-color:black
}
button[type="submit"]{background:white
}
.modal{border-radius:0.5vh !important;box-shadow:0 0px 0px 0 rgba(0,0,0,0.24),0 0px 0px 0 rgba(0,0,0,0.19)
}
.with.left-fixed-sidebar header .navbar,.with.left-fixed-sidebar footer,.with.left-fixed-sidebar main{padding-left:0
}
.with.top-navbar main>.content{height:calc(100vh - 7.2em);min-height:28em;padding-top:0rem
}
.footer{background:#12515c
}
.footer div{text-align:center;font-weight:500
}
.vt-submit-btn{border-radius:0.5vh;color:#328782;background:#ffffff;font-weight:300
}
@media (min-width: 0px) and (max-width: 740px){
.vt-submit-btn{padding:0.5vh 1.5vw;font-size:1.4rem
}
}
@media screen and (min-width: 767px){
.hidden-sm-and-up{display:none !important
}
}
.sidebar--open{width:200px
}
.clearfix:before,.clearfix:after{content:" ";display:table
}
.clearfix:after{clear:both
}



