
.navbar__group-header--active,.navbar__item--active{background:#b3dfdc;box-shadow:0px -4px 0px #0097A7 inset;color:#e1e1e1
}
.navbar__group-header:hover,.navbar__item:hover{background:rgba(111,178,179,0.2)
}
li a.navbar__item,.navbar__group-header{text-decoration:none;color:#03735b
}
.navbar__group .navbar__items .navbar__item:hover{background-color:rgba(111,178,179,0.2)
}
.navbar__group .navbar__items{color:#03735b;font-weight:100;background-color:#ffffff
}
.navbar__logo{color:#03735b;font-weight:300;padding:0
}
.navbar{height:4rem
}
.navbar ul{font-weight:400
}
.navbar__title{margin:0%;color:#03735b;font-size:3rem;font-weight:300
}
.navbar__side-icon{margin:0 0 0 14px;color:teal
}
@media screen and (max-width: 980px){
.navbar__items{font-size:15px
}
.navbar__title{font-size:4vw
}
}
@media screen and (max-width: 640px){
.navbar__items{font-size:15px
}
.navbar__title{font-size:6vw
}
}
#profileNav{cursor:pointer
}



